/* Reset and base styles  */
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Karla:wght@300;400;700;800&display=swap");
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

:focus,
:active {
  /*outline: none;*/
}

a:focus,
a:active {
  /* outline: none;*/
}

/* Links */
a, a:link, a:visited {
  /* color: inherit; */
  text-decoration: none;
  /* display: inline-block; */
}

a:hover {
  /* color: inherit; */
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

@font-face {
  font-family: "icomoon";
  src: url("/app/fonts/icons.eot?afwhip");
  src: url("/app/fonts/icons.eot?afwhip#iefix") format("embedded-opentype"), url("/app/fonts/icons.ttf?afwhip") format("truetype"), url("/app/fonts/icons.woff?afwhip") format("woff"), url("/app/fonts/icons.svg?afwhip#icons") format("svg");
  font-weight: normal;
  font-style: normal;
}
.icomoon {
  position: relative;
}

.icomoon:after,
.icomoon:before {
  font-family: icomoon !important;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
}

html {
  scroll-behavior: smooth;
}

body {
  font: 16px "Karla", sans-serif;
  font-weight: 300;
  color: #888;
}

h2 {
  color: #00559E;
  font-family: "Lato", sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 50px;
}

.pixel-arrow,
.pixel-cross {
  display: flex;
  flex-wrap: wrap;
  position: absolute;
}
.pixel-arrow .pixel,
.pixel-cross .pixel {
  width: 24px;
  height: 24px;
}

.pixel-arrow {
  width: 48px;
  height: 48px;
}
.pixel-arrow .pixel-1 {
  background: #040404;
  opacity: 0.8;
}
.pixel-arrow .pixel-2 {
  background: #fff;
}
.pixel-arrow .pixel-3 {
  background: #040404;
}
.pixel-arrow .pixel-4 {
  background: #08121D;
  opacity: 0.9;
}

.pixel-cross {
  width: 72px;
  height: 72px;
}
.pixel-cross .pixel {
  background: #040404;
}
.pixel-cross .pixel-1, .pixel-cross .pixel-3, .pixel-cross .pixel-7, .pixel-cross .pixel-9 {
  opacity: 0;
}
.pixel-cross .pixel-2, .pixel-cross .pixel-8 {
  opacity: 0.6;
}
.pixel-cross .pixel-4 {
  background: #08121D;
  opacity: 0.8;
}
.pixel-cross .pixel-5 {
  opacity: 0.9;
}
.pixel-cross .pixel-6 {
  opacity: 0.4;
}

.donat-button {
  font-weight: 700;
  background: #063758;
  border: 4px solid #E0F300;
  width: 200px;
  height: 56px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #E0F300;
}
.donat-button:hover {
  background: #E0F300;
  border: 4px solid #063758;
  color: #063758;
}

header {
  position: relative;
  display: flex;
  align-items: center;
  height: 96px;
  padding: 20px;
  background: black;
}
header a {
  color: #fff;
}
header .mobile-header,
header .mobile-menu {
  display: none;
}
header .head-half {
  flex: 0 0 50%;
  display: flex;
  align-items: center;
}
header .logo-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 142px;
  height: 144px;
  left: 181px;
  top: 0;
  background: #00559E;
  border-radius: 0 0 8px 8px;
  z-index: 200;
  padding-top: 24px;
}
header .navigate {
  display: flex;
}
header .navigate li {
  margin-right: 40px;
}
header .navigate li a:hover {
  color: #00559E;
}
header .head-contact {
  margin-left: 347px;
  display: flex;
}
header .head-contact li {
  color: #fff;
}
header .head-contact li.location {
  margin-right: 80px;
}
header .head-contact li.location:before, header .head-contact li.phone:before {
  left: -32px;
  font-size: 20px;
}
header .head-contact li.location:before {
  content: "\e901";
  top: 0;
}
header .head-contact li.phone:before {
  content: "\e903";
  top: 2px;
}
header .donat-button {
  margin-right: 48px;
}
header .language {
  margin-right: 0;
}
header .language span {
  display: inline-block;
}
header .language .ukr,
header .language .en {
  width: 27px;
  height: 18px;
  border-radius: 3px;
  position: relative;
}
header .language .ukr {
  background: no-repeat url(/img/icon/Flag_of_Ukraine.jpg);
  margin-right: 15px;
}
header .language .en {
  background: no-repeat url(/img/icon/Flag_of_the_United_Kingdom.jpg);
}
header .language .ukr.active:after,
header .language .en.active:after {
  content: "";
  position: absolute;
  width: 8px;
  height: 2px;
  left: 10px;
  bottom: -4px;
  background: #FFF;
  border-radius: 10px;
}
header .mobile-header {
  display: none;
}

.presentation-block {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
}
.presentation-block .arrow-pixel {
  position: absolute;
  bottom: 50px;
  left: 50%;
  margin-left: -10px;
  height: 20px;
  width: 25px;
}
.presentation-block .arrow-pixel a {
  height: 20px;
  width: 20px;
  display: block;
}
.presentation-block .arrow-pixel a:after {
  content: "\e904";
  color: #E0F300;
  font-size: 25px;
}
.presentation-block .left-blocks .info-block {
  position: relative;
}
.presentation-block .left-blocks .info-block .bg-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.presentation-block .left-blocks .info-block .info-content {
  display: none;
  position: absolute;
  align-items: center;
  flex-direction: column;
  left: 0;
  top: 0;
  padding: 20px;
  height: 100%;
}
.presentation-block .left-blocks .info-block .info-content img {
  margin-bottom: 20px;
  max-width: 170px;
  height: auto;
  max-height: 45%;
}
.presentation-block .left-blocks .info-block .info-content p {
  color: #fff;
  font-size: 12px;
  line-height: 14px;
}
.presentation-block .left-blocks .red-block {
  height: 34%;
}
.presentation-block .left-blocks .red-block .info-content {
  background: #AE0F0F;
  flex-direction: row;
}
.presentation-block .left-blocks .red-block .info-content img {
  max-width: 170px;
  max-height: 50%;
  width: 100%;
  margin: 0 40px 0 0;
}
.presentation-block .left-blocks .red-block:hover .info-content {
  display: flex;
}
.presentation-block .left-blocks .yellow-block {
  height: 50%;
}
.presentation-block .left-blocks .yellow-block .info-content {
  background: #E0F300;
}
.presentation-block .left-blocks .yellow-block .info-content p {
  color: #888;
}
.presentation-block .left-blocks .yellow-block:hover .info-content {
  display: flex;
}
.presentation-block .left-blocks .grey-block {
  height: 50%;
}
.presentation-block .left-blocks .grey-block .info-content {
  background: #6A6864;
}
.presentation-block .left-blocks .grey-block:hover .info-content {
  display: flex;
}
.presentation-block .left-blocks .black-block .info-content {
  background: #000;
}
.presentation-block .left-blocks .black-block .info-content img {
  object-fit: cover;
}
.presentation-block .left-blocks .black-block:hover .info-content {
  display: flex;
}
.presentation-block .left-blocks .bottom-info-block {
  display: flex;
  height: 66%;
}
.presentation-block .right-blocks .blue-block {
  padding: 102px 20px 0 176px;
  height: 71%;
  background: #00559E;
}
.presentation-block .right-blocks .blue-block h1 {
  font-size: 40px;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  line-height: 72px;
  color: #fff;
  margin-bottom: 40px;
}
.presentation-block .right-blocks .blue-block h3 {
  font-size: 18px;
  line-height: 21px;
  color: #fff;
  font-weight: 800;
  margin-bottom: 20px;
}
.presentation-block .right-blocks .blue-block p {
  margin-bottom: 45px;
  line-height: 24px;
  color: #d9edff;
}
.presentation-block .right-blocks .blue-block .donat-button {
  width: 300px;
}
.presentation-block .right-blocks .colorless-block {
  height: 29%;
  display: flex;
}

.wrapper .beginning {
  display: flex;
  padding: 120px 0 0 180px;
  height: 100vh;
}
.wrapper .beginning img {
  width: auto;
  height: auto;
  max-height: 99%;
  margin-right: 150px;
}
.wrapper .beginning .beginning-text {
  position: relative;
  padding: 100px 180px 0 0;
}
.wrapper .beginning .beginning-text p {
  font-size: 14px;
  margin-top: 40px;
}
.wrapper .beginning .beginning-text .pixel-arrow {
  left: -60px;
}
.wrapper .beginning .beginning-text .pixel-cross {
  right: 90px;
  top: 115px;
}
.wrapper .about {
  display: flex;
  padding: 120px 0 0 180px;
}
.wrapper .about img {
  margin: 115px 150px 0 0;
  max-width: 560px;
  height: auto;
  object-fit: contain;
}
.wrapper .about .about-text h2 {
  margin-bottom: 65px;
}
.wrapper .about .about-text p {
  margin-top: 40px;
}
.wrapper .about-arrow-box {
  height: 68px;
  position: relative;
  margin: 250px 0 150px;
  display: flex;
  justify-content: center;
}
.wrapper .about-arrow-box .pixel-arrow {
  transform: rotate(-45deg);
}
.wrapper .help-image {
  display: flex;
  padding: 0 25px;
}
.wrapper .help-image img {
  margin-right: 10px;
  width: 41%;
}
.wrapper .help-image img:nth-child(2) {
  width: 58%;
}

@media screen and (max-width: 1550px) {
  header .head-half {
    flex: 0 0 auto;
  }
  .head-contact {
    flex-direction: column;
  }
  .head-contact li {
    padding: 5px 0;
  }
  .presentation-block .right-blocks .blue-block {
    padding: 40px 20px 0 80px;
  }
  .presentation-block .right-blocks .blue-block h1 {
    font-size: 36px;
    line-height: 58px;
  }
}
@media screen and (max-width: 1330px) {
  header .donat-button,
header .navigate li {
    margin-right: 20px;
  }
}
@media screen and (max-width: 1220px) {
  header .logo-block {
    left: 80px;
  }
  header .head-contact {
    margin-left: 250px;
  }
}
@media screen and (max-width: 1140px) {
  header .navigate {
    flex-direction: column;
  }
  .presentation-block .right-blocks .blue-block h1 {
    font-size: 30px;
    line-height: 46px;
  }
}
@media screen and (max-width: 940px) {
  header .logo-block {
    left: 20px;
  }
  header .head-contact {
    margin-left: 190px;
  }
}
@media screen and (max-width: 887px) {
  header {
    height: 60px;
    padding: 0 16px;
  }
  header .head-half {
    display: none;
  }
  header .mobile-header {
    display: flex;
    flex: 0 0 100%;
    justify-content: space-between;
  }
  header .mobile-header .logo-block {
    padding-top: 18px;
    left: 50%;
    margin-left: -48px;
    width: 96px;
    height: 104px;
  }
  header .mobile-header .logo-block img {
    width: 48px;
  }
  header .mobile-header .logo-block .blue-logo {
    display: none;
  }
  header .mobile-header .logo-block.active {
    background: #fff;
  }
  header .mobile-header .logo-block.active .white-logo {
    display: none;
  }
  header .mobile-header .logo-block.active .blue-logo {
    display: block;
  }
  header .mobile-header .burger-menu,
header .mobile-header .cross {
    color: #fff;
  }
  header .mobile-header .burger-menu {
    width: 20px;
    height: 12px;
  }
  header .mobile-header .burger-menu:after {
    content: "\e906";
    font-size: 26px;
    top: -5px;
  }
  header .mobile-header .burger-menu.active {
    width: 16px;
    height: 16px;
  }
  header .mobile-header .burger-menu.active:after {
    content: "\e905";
    top: 0;
    font-size: 16px;
  }
  header .mobile-header .ukr.active:after,
header .mobile-header .en.active:after {
    bottom: -8px;
  }
  header .mobile-menu {
    height: calc(100vh - 60px);
    width: 100%;
    position: absolute;
    top: 60px;
    left: 0;
    z-index: 100;
    background: #000;
    padding-left: 42px;
    display: none;
    flex-direction: column;
  }
  header .mobile-menu.active {
    display: flex;
  }
  header .mobile-menu .head-contact {
    margin: 84px 0 55px;
    flex-direction: column;
  }
  header .mobile-menu .head-contact li {
    text-indent: 36px;
    margin-bottom: 25px;
  }
  header .mobile-menu .navigate {
    flex-direction: column;
  }
  header .mobile-menu .navigate li {
    margin-bottom: 25px;
  }
  header .mobile-menu .navigate li a:hover {
    text-decoration: none;
  }
}